











import {Component, Prop, Vue} from 'vue-property-decorator';

@Component
export default class PanThumb extends Vue {
    @Prop(String)
    private image!: string;
    @Prop(Number)
    private zIndex!: number;
    @Prop(String)
    private width!: string;
    @Prop(String)
    private height!: string;
}
